  .alert-message {
    float: left ;
    width: 90%;
    padding: 1.25rem;
    padding-top: 0;
    padding-bottom: 0;
  }
}

.alert-message :last-child {
  margin-bottom: 0;
}

@media (min-width: 768px) {
  .alert-title {
    margin-top: -0.625rem;
  }
}

@media (min-width: 768px) {
  .alert-copy {
    margin-top: 0.3125rem;
  }
}

.alert-action {
  width: 100%;
}

@media (min-width: 768px) {
  .alert-action {
    float: right ;
    width: inherit;
  }
}

